    flex-direction: column;
    width: 220px;
  }
}
@media (max-width: 767px) {
  .accordion__list {
    display: none;
  }
}
@media (max-width: 767px) {
  .accordion__list--opened {
    display: flex;
    flex-direction: column;
  }
