.accordion__content {
  margin-bottom: 5px;
}

.accordion__link {
  font-family: "PT Mono", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #ffffff;
  opacity: 0.7;
}

@media (max-width: 767px) {
  .accordion__wrapper {
    position: relative;
    padding: 20px 0;
    padding-bottom: 15px;
  }
}

.accordion__toggle {
  display: none;
}
@media (max-width: 767px) {
  .accordion__toggle {
    display: block;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background-color: transparent;
    border: none;
    cursor: pointer;
  }
  .accordion__toggle::after, .accordion__toggle::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    width: 15px;
    height: 1px;
    background-color: #ffffff;
  }
  .accordion__toggle::before {
    transform: rotate(-270deg);
  }
}
@media (max-width: 767px) {
  .accordion__toggle--opened::before {
    transform: rotate(540deg);
  }
}

